.work-card {
    padding: 8px;
    width: 100%;
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row-reverse;
}
.work-logo {
    height: 120px;
}

.work-info {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}

.work-dates {
    color: black;
}

.work-desc {
    text-align: left;
    display: -webkit-box;
    /* -webkit-line-clamp: 2;
    -webkit-box-orient: horizontal; */
    overflow: hidden ;
    text-overflow: ellipsis;
    max-width: 80%;
}

.work-designation{
    font-size: 20px;
    font-weight: 600;
}



@media only screen and (max-width: 720px) {
    .work-card{
        width: 100%;
    }
}